



































































































	export default {
		data() {
			return {
				tabLists: [
          {
            name: '收入明细'
          },
          {
            name: '红包明细'
          },
          {
            name: '提现记录'
          }
        ],
        tabIndex: 0,
        earningLists: [],
        currentPage: 1, // 当前页
        maxPage: 1, // 最大页
        loading: false, // 加载中
        searchStr: '', // 搜索字段
        searching: false, // 搜索状态
        inputing: false
			};
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      },
      isLoad() {
        return parseInt(this.maxPage) <= parseInt(this.currentPage) && this.earningLists.length > 0
      },
      tipsText() {
        return '尚无' + this.tabLists[this.tabIndex].name
      },
      showSearch() {
        return ((this.earningLists.length > 0 && !this.loading) || this.searching) && this.tabIndex === 0
      }
    },
		onLoad(query) {
			if (parseInt(query.tab) <= this.tabLists.length - 1 && parseInt(query.tab) >= 0) this.tabIndex = parseInt(query.tab)
      this.updateLists(this.tabIndex)
		},
    methods: {
      gotoCash() {
        this.$navigateTo({
        	url: '/pages/mine/cash'
        })
      },
      tabClick(n) {
        if (this.tabIndex === n) return
        if (this.loading) {
          uni.showToast({
          	title: '正在加载数据',
            icon: 'none'
          })
          return
        }
        this.tabIndex = n
        this.searchStr = ''
        this.searching = false
        this.currentPage = 1
        this.updateLists(this.tabIndex)
      },
      updateLists(n) {
        if (this.loading) return
        this.loading = true
        if (parseInt(this.currentPage) <= 1) this.earningLists = []
        switch (n) {
          case 0:
            this.$http({
              url: this.$apis.earningList,
              method: 'GET',
              data: {
                page: this.currentPage,
                keyword: this.searchStr
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  if (parseInt(this.currentPage) <= 1) this.earningLists = []
                	this.currentPage = data.data.current_page
                	this.maxPage = data.data.last_page
                	let oldArr = this.earningLists
                	let newArr = data.data.data
                	this.earningLists = oldArr.concat(newArr)
                	if (this.searchStr.length > 0) this.searching = true
                  setTimeout(() => {
                    this.loading = false
                  }, 600)
                } else {
                	this.searching = false
                  this.loading = false
                  uni.showToast({
                  	title: '获取收支明细失败',
                    icon: 'none'
                  })
                }
              }
            })
            break
          case 1:
            this.$http({
              url: this.$apis.redpacketList,
              method: 'GET',
              data: {
                page: this.currentPage
              },
              cb: (err, data) => {
                if (!err && data.success) {
                  if (parseInt(this.currentPage) <= 1) this.earningLists = []
                	this.currentPage = data.data.current_page
                	this.maxPage = data.data.last_page
                	let oldArr = this.earningLists
                	let newArr = data.data.data
                	this.earningLists = oldArr.concat(newArr)
                	if (this.searchStr.length > 0) this.searching = true
                  setTimeout(() => {
                    this.loading = false
                  }, 600)
                } else {
                	this.searching = false
                  this.loading = false
                  uni.showToast({
                  	title: '获取红包明细失败',
                  	icon: 'none'
                  })
                }
              }
            })
            break
          case 2:
            this.$http({
              url: this.$apis.withdrawLists,
              method: 'GET',
              data: {
                page: this.currentPage
              },
              cb: (err, data) => {
                if (!err && data.success === true) {
                	this.currentPage = data.data.current_page
                	this.maxPage = data.data.last_page
                	let oldArr = this.earningLists
                	let newArr = data.data.data
                	this.earningLists = oldArr.concat(newArr)
                	if (this.searchStr.length > 0) this.searching = true
                } else {
                	this.searching = false
                  uni.showToast({
                  	title: '获取提现明细失败',
                    icon: 'none'
                  })
                }
                this.loading = false
              }
            })
            break
        }
      },
      search() {
        if (this.searchStr.length <= 0) {
          uni.showToast({
          	title: '请输入搜索内容',
            icon: 'none'
          })
          return
        }
        this.currentPage = 1
        this.updateLists(this.tabIndex)
      },
      resetSearch() {
        this.currentPage = 1
        this.searchStr = ''
        this.searching = false
        this.updateLists(this.tabIndex)
      }
    }
	}
